@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movie {
    margin: 10px auto;
    width: 275px;
    color: #fff;
    box-shadow: 3px 3px 8px 1px rgba(0,0,0,0.44);
    position: relative;
    overflow: hidden;
    border-radius: 25px;
}

.movie__image {
    width: 100%;
    height: 425px;
    object-fit: cover;
    margin-bottom: -4px;
}

.movie__infoReveal {
    display: block !important;
}

.movie__details {
    background-color: #034748;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.movie__details h4 {
    font-weight: 500;
    max-width: 220px;
}

.movie__infoIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px #fff solid;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    cursor: pointer;
}

.movie__popup {
    display: none;
    margin: auto;
    width: 95vw;
    height: 95vh;
    border: 3px solid #f7d12a;
    border-radius: 25px;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #034748;
    color: #dfdfdf;
    overflow: auto;
    z-index: 2;
}

.movie__popupContainer {
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.movie__popupHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.icon {
    cursor: pointer;
}

table {
    border-spacing: 0 20px;
}

.movie__infoCategories {
    width: 30%;
    vertical-align:top;
}

.movie__infoDetails {
    width: 70%;
}

.movie__link {
    display: flex;
    justify-content: center;
    align-items: center;
}

.movie__link a {
    width: 70%;
    height: 40px;
    background-color: #25313d;
    color: #dfdfdf;
    border: 1px #000 solid;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    padding: 10px;
}

.movie__link a:active {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

.movie__link a img {
    width: 50px;
    height: auto;
    margin-left: 5px;
}

.modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 500%;
    background-color: rgba(105, 105, 105, 0.548);
    z-index: 1;
}

.modal__display {
    display: block;
}

@media only screen and (min-width: 500px){
    .movie__popup {
        width: 500px !important;
        height: 700px !important;
    }
}
.movies__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    justify-content: center;
}
.search__container {
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.search {
    height: 50px;
    position: relative;
}

.search__input {
    background-color: #034748;
    color: #fff;
    border: 0;
    border-radius: 50px;
    font-size: 14px;
    height: 58px;
    width: 50px;
    transition: width 0.3s ease;
    padding-left: 10px;
    box-shadow: 3px 3px 8px 1px rgba(0,0,0,0.44);
}

.search__button {
    background-color: #034748;
    color: #fff;
    border: 0;
    border-radius: 50px;
    cursor: pointer;
    font-size: 24px;
    position: absolute;
    left: 0;
    height: 58px;
    width: 50px;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    box-shadow: 8px 3px 8px 1px rgba(0,0,0,0.44);
}

.search__button, .search__input:focus {
    outline: none;
}

.search.active .search__input {
    width: 248px;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
}

.search.active .search__button {
    -webkit-transform: translateX(198px);
            transform: translateX(198px);
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
* {
  box-sizing: content-box;
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
}

body {
  background-color: #25313d;
}

.App {
  display: flex;
  justify-content: center;
}

.container {
  width: 1200px;
}

